import React, { useState, useEffect} from "react";
//import axios from 'axios'

const Notes = ({ data }) => {
    return (
      <div>
        <ul>
          {data && data.map((item, index) => <li key={index}>
            {item.Parking_Lot}
            &nbsp;
            {"| Space Available: " + item.space_available}
            &nbsp;
            {"| Availability: " + item.Availability}
            &nbsp;
            {"| Total Parking Space: " + item.Total_Parking_Space}
          </li>)}
        </ul>
      </div>
    );
};

const API = process.env.NODE_ENV === 'production' ? 'https://mern-version1.herokuapp.com' : 'http://localhost:5000';
const ParkingList = () => {
    const [notes, setNotes] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [isError, setIsError] = useState(false);
    const fetchData = () => {
      fetch(API + '/api/parkings', { method: 'GET' })//http://localhost:5000
        .then((response) => response.json())
        .then((data) => {
          setIsLoading(false);
          setNotes(data);
        })
        .catch((error) => {
          setIsLoading(false);
          setIsError(true);
          console.log(error);
        });
    };
    useEffect(() => {
      fetchData();
    }, []);
    if (isLoading) {
      return <div>Loading...</div>;
    }
    return (
      <div>
        <h1>List of Parking Lots</h1>
        {notes && <Notes data={notes} />}
        {isError && <div>Error fetching data.</div>}
      </div>
    );
  };
export default ParkingList